import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export class Mentorsprofile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      email: "",
      company: "",
      languge: "",
      photo: null
    };
  }
  componentDidMount() {
    fetch(`mentorProfile.json`)
      .then(res => res.json())
      .then(data => {
        console.log(data.name);
        this.setState({
          name: data.name,
          email: data.email,
          company: data.company,
          languages: data.languages,
          photo: data.photo
        })
      })
  }
  render() {
    return (
      <div>
          <nav className= "navbar navbar-expand-md navbar-light bg-light sticky-top">
       <ul >
           <li style={{listStyleType: "none"}}><Link to="/"><i className="fa fa-home"></i></Link></li>
       </ul>
       </nav>
        <div className="container mt-4 mb-4">
          <div className="row">
            <div className="col-4">
              <img className="rounded-circle w-100" alt='profile' src='data:image/jpeg;base64,' />
              <button className='btn btn-primary'>Edit Profile</button>
            </div>
            <div className="col-8">
              <ul className="list-group">
                <li className="list-group-item">Name: {this.state.name}</li>
                <li className="list-group-item">Email: {this.state.email}</li>
                <li className="list-group-item">Company: {this.state.company}</li>
                <li className="list-group-item">Skills: {this.state.languages}</li>
              </ul>

            </div>

          </div>
        </div>
      </div>
    )
  }
};

export default Mentorsprofile;
